/* sidebar.css */

.container {
    position: relative;
    width: 200px; /* Adjust the width as needed */
    background-color: #053a41; /* Sidebar background color */
    padding: 10px;
    height: 100vh;
    border-radius: 25px;
    margin: 10px 10px 10px 25px;
  }
  
  .sidebar {
    position: relative;
    height: 100vh;
  }
  
  /* Style for the "Admin" and "Blog" links */
  .button-css {
    display: block;
    text-decoration: none;
    color: #fff; /* Link text color */
    background-color: #053a41; /* Link background color */
    padding: 10px 15px;
    margin-bottom: 10px;
    border: 1px solid #222; /* Link border */
    border-radius: 5px;
    transition: all 0.3s ease; /* Smooth transition on hover */
  
    /* 3D effect */
    transform: perspective(400px) rotateY(0deg);
    transform-origin: 0% 100%;
    backface-visibility: hidden;
  }
  
  /* Hover effect for the "Admin" and "Blog" links */
  .button-css:hover {
    background-color: rgb(2, 23, 23); /* Link background color on hover */
    transform: perspective(400px) rotateY(-5deg);
    box-shadow: 5px 0 10px rgba(0, 0, 0, 0.2); /* Add a subtle shadow on hover */
  }
  
