/* Styling for Signup Container */
.signup-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

/* Styling for Signup Heading */
.signup-heading {
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
}

/* Styling for Signup Form */
.signup-form {
  display: flex;
  flex-direction: column;
}

/* Styling for Signup Input Fields */
.signup-input {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

/* Styling for Signup Button */
.signup-button {
  background-color: #007bff;
  color: #fff;
  font-size: 18px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.signup-button:hover {
  background-color: #0056b3;
}

/* Styling for Signup Paragraph */
.signup-p {
  text-align: center;
  margin-top: 10px;
}

/* Styling for Log In Link */
.signup-span {
  color: #007bff;
  cursor: pointer;
}

