/* Login.css or your custom CSS file */

/* Styling for the login container */
.login-container {
  background-color: #f0f0f0; /* Background color for the container */
  padding: 20px;
  border: 1px solid #ddd; /* Border for the container */
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Shadow effect */
  max-width: 400px; /* Adjust the maximum width as needed */
  margin: 0 auto; /* Center the container horizontally */
}

/* Styling for the login form */
.login-form {
  display: flex;
  flex-direction: column;
}

/* Styling for the form inputs */
.login-input {
  border: 1px solid #ccc; /* Border for the input fields */
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

/* Styling for the submit button */
.login-button {
  background-color: #007bff; /* Button background color */
  color: #fff; /* Button text color */
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

/* Styling for the submit button on hover */
.login-button:hover {
  background-color: #0056b3; /* Button background color on hover */
}

/* Styling for the "sign up" link */
.login-p {
  text-align: center;
  margin-top: 10px;
  font-size: 14px;
}

.login-span {
  color: #007bff; /* Link text color */
  cursor: pointer;
}

/* Styling for the error message */
.login-error-message {
  color: #ff5733; /* Error message text color */
  text-align: center;
  margin-top: 10px;
}
